      if (this.planItem["res"] == null) this.planItem["res"] = "";
      axios
        .post("http://localhost:8081/user/addPlan", this.planItem)
        .then(function (response) {
          console.log(response);
        });
      setExploitStatus(this.formData["id"], "开发中");
      this.planItem = {};
      this.$router.go(0);
    },
    addItem() {
      this.dialogShow = true;
      this.dialogShow2 = true;
    },
    deleteClick() {},
    async handleEdit(row) {
      if (
        row["exploitStatus"] == "开发成功" ||
        row["exploitStatus"] == "开发失败"
      )
        this.kaifashibai = false;
      else
        this.kaifashibai = true;

      console.log(row);
      this.dialogShow = true;
      console.log(this.dialogShow);
      this.formData = Object.assign({}, row);
      this.formDataCopy = Object.assign({}, row);
      let res = await getItems(row["id"]);
      console.log(res.data);
      this.itemData = res.data;
      
    },
    submitForm() {
      this.dialogShow = false;
      let index = this.tableData.findIndex(
        (item, index) => item.id === this.formData.id
      );
      console.log(index);
      this.tableData.splice(index, 1, this.formData);
      this.$router.go(0);
    },
    async submitForm_() {
      this.dialogShow4 = false;
      //setYinXiao(this.formData);
      this.newData["creator"] = this.userName;
      this.newData["exploitStatus"] = "待开发";
      let time = dayjs().format("YYYY-MM-DD");
      this.newData["createTime"] = time;
      console.log(this.str);
      if (this.newData["allocator"] == null) {
        this.newData["status"] = 0;
        this.newData["allocateTime"] = "";
      } else {
        this.newData["status"] = 1;
        this.newData["allocateTime"] = time;
      }
      console.log(this.newData);
      console.log(this.newData["creator"]);
      axios
        .post("http://localhost:8081/user/addYinXiao", this.newData)
        .then(function (response) {
          console.log(response);
        });
      this.newData = {};
      this.$router.go(0);
    },
    resetForm() {
      this.formData = Object.assign({}, this.formDataCopy);
    },
    confirmEvent(val) {
      let index = this.itemData.findIndex((item) => item.id === val.id);
      this.itemData.splice(index, 1);
      deleteplan(val.id);
      this.$router.go(0);
    },
    exit() {
      this.$router.push({path: '/'});
    }
  },
};
</script>


