<script>
import dayjs from "dayjs";
import pubsub from "pubsub-js";
import { getAllYinXiao, getAllorders, getAllContactinfos } from "@/api/index";
import axios from "axios";
import { createComponent } from "echarts-for-vue";
import * as echarts from "echarts";
export default {
  data() {
    return {
      //time: "2022-3-14 21:34:18",
      kaifashibai: true,
      showTopRight: false,
      tip: "数据看板",
      userName: "",
      baseShow: true,
      startTime: "",
      endTime: "",
      saleValue: 10000,
      newOrders: 15,
      newCustomers: 15,
      newContacts: 15,
      newSales: 15,
      successCustomers: 15,
      ordersData: [],
      ordersDataCopy: [],
      saleData: [],
      allCustomers: [],
      contactData: [],

      option: {
        xAxis: {
          type: "category",
          data: [],
        },
        yAxis: {
          type: "value",
        },
        series: [
          {
            data: [],
            type: "bar",
            showBackground: true,
            backgroundStyle: {
              color: "rgba(180, 180, 180, 0.2)",
            },
            itemStyle: {
              normal: {
                label: {
                  show: true,
                  position: "top",
                  textStyle: {
                    color: "black",
                    fontSize: 10,
                  },
                },
              },
            },
          },
        ],
        title: {
          text: "销售金额(元)",
          left: 1,
          textStyle: {
            fontSize: 14,
          },
        },
      },
      option2: {
        xAxis: {
          type: "category",
          data: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"],
        },
        yAxis: {
          type: "value",
        },
        series: [
          {
            data: [820, 932, 901, 934, 1290, 1330, 1320],
            type: "line",
            smooth: true,
          },
        ],
        title: {
          text: "销售金额(元)",
          left: 1,
          textStyle: {
            fontSize: 14,
          },
        },
      },
    };
  },
  components: {
    //VTable, VPagination
  },
  props: {},
  async mounted() {
    this.check();
    pubsub.subscribe("sendMsg", (e, msg) => {
      this.showTopRight = msg;
      console.log(this.showTopRight);
    });
  },
  async created() {
    let res = await getAllorders();
    this.ordersData = res.data;
    this.ordersDataCopy = res.data;
    var sumSaleValue = 0;
    for (let val of res.data) {
      sumSaleValue = sumSaleValue + Number(val["orderValue"]);
    }
    this.saleValue = sumSaleValue;
    this.newOrders = res.data.length;

    let res_ = await getAllYinXiao();
    this.saleData = res_.data;
    console.log(this.saleData);
    let setCustomers = new Set();
    for (let val of this.saleData) {
      setCustomers.add(val["customerName"]);
    }
    this.newCustomers = setCustomers.size;
    this.newSales = this.saleData.length;
    this.allCustomers = setCustomers;

    let res_2 = await getAllContactinfos();
    console.log(res_2.data);
    this.contactData = res_2.data;

    let ordersValue = new Map();
    for (let val of this.ordersData) {
      console.log(val);
      if (ordersValue.has(val["createTime"])) {
        ordersValue.set(
          val["createTime"],
          ordersValue.get(val["createTime"]) + Number(val["orderValue"])
        );
      } else {
        ordersValue.set(val["createTime"], Number(val["orderValue"]));
      }
    }
    console.log(ordersValue);
    this.option["xAxis"]["data"] = [];
    this.option["series"][0]["data"] = [];
    this.option2["xAxis"]["data"] = [];
    this.option2["series"][0]["data"] = [];
    const iterator1 = ordersValue.entries();
    var index = 0;
    for (index; index < ordersValue.size; index++) {
      var tem = iterator1.next().value;
      this.option["xAxis"]["data"].push(tem[0]);
      this.option["series"][0]["data"].push(tem[1]);
      this.option2["xAxis"]["data"].push(tem[0]);
      this.option2["series"][0]["data"].push(tem[1]);
    }
    console.log(this.option["series"]["data"]);
  },
  methods: {
    check() {
      console.log(this.startTime);
      console.log(this.endTime);
      var splitStartTime = this.startTime.split("-");
      var splitEndTime = this.endTime.split("-");
      console.log(Number(splitEndTime[0]));
      var sumSaleValue = 0;
      var sumOrders = 0;
      for (let val of this.ordersData) {
        let temVal = val["createTime"].split("-");
        let temSum =
          Number(temVal[0]) * 12 * 30 +
          Number(temVal[1]) * 30 +
          Number(temVal[2]);
        let startSum =
          Number(splitStartTime[0]) * 12 * 30 +
          Number(splitStartTime[1]) * 30 +
          Number(splitStartTime[2]);
        let endSum =
          Number(splitEndTime[0]) * 12 * 30 +
          Number(splitEndTime[1]) * 30 +
          Number(splitEndTime[2]);
        console.log(startSum);
        console.log(endSum);
        console.log(temSum);
        if (temSum >= startSum && temSum <= endSum) {
          sumOrders++;
          sumSaleValue = sumSaleValue + Number(val["orderValue"]);
        }
      }
      this.saleValue = sumSaleValue;
      this.newOrders = sumOrders;

      let setCustomers = new Set();
      var sumNewSales = 0;
      this.allCustomers = new Set();
      let setSuccessCustomers = new Set();
      for (let val of this.saleData) {
        let temVal = val["createTime"].split("-");
        let temSum =
          Number(temVal[0]) * 12 * 30 +
          Number(temVal[1]) * 30 +
          Number(temVal[2]);
        let startSum =
          Number(splitStartTime[0]) * 12 * 30 +
          Number(splitStartTime[1]) * 30 +
          Number(splitStartTime[2]);
        console.log(temSum);
        if (temSum <= startSum) {
          this.allCustomers.add(val["customerName"]);
        }
      }
      console.log(this.allCustomers);
      for (let val of this.saleData) {
        let temVal = val["createTime"].split("-");
        let temSum =
          Number(temVal[0]) * 12 * 30 +
          Number(temVal[1]) * 30 +
          Number(temVal[2]);
        let startSum =
          Number(splitStartTime[0]) * 12 * 30 +
          Number(splitStartTime[1]) * 30 +
          Number(splitStartTime[2]);
        let endSum =
          Number(splitEndTime[0]) * 12 * 30 +
          Number(splitEndTime[1]) * 30 +
          Number(splitEndTime[2]);
        if (temSum >= startSum && temSum <= endSum) {
          if (!this.allCustomers.has(val["customerName"]))
            setCustomers.add(val["customerName"]);
          sumNewSales++;
          if (
            !this.allCustomers.has(val["customerName"]) &&
            val["exploitStatus"] == "开发成功"
          ) {
            setSuccessCustomers.add(val["customerName"]);
          }
        }
      }
      this.newCustomers = setCustomers.size;
      this.newSales = sumNewSales;
      this.successCustomers = setSuccessCustomers.size;

      let setNewContacts = new Set();
      let allContacts = new Set();
      for (let val of this.contactData) {
        let temVal = val["createTime"].split("-");
        let temSum =
          Number(temVal[0]) * 12 * 30 +
          Number(temVal[1]) * 30 +
          Number(temVal[2]);
        let startSum =
          Number(splitStartTime[0]) * 12 * 30 +
          Number(splitStartTime[1]) * 30 +
          Number(splitStartTime[2]);
        console.log(temSum);
        if (temSum <= startSum) {
          allContacts.add(val["name"]);
        }
      }
      console.log(allContacts);
      for (let val of this.contactData) {
        if (val["createTime"] === null) continue;
        let temVal = val["createTime"].split("-");
        let temSum =
          Number(temVal[0]) * 12 * 30 +
          Number(temVal[1]) * 30 +
          Number(temVal[2]);
        let startSum =
          Number(splitStartTime[0]) * 12 * 30 +
          Number(splitStartTime[1]) * 30 +
          Number(splitStartTime[2]);
        let endSum =
          Number(splitEndTime[0]) * 12 * 30 +
          Number(splitEndTime[1]) * 30 +
          Number(splitEndTime[2]);
        if (temSum >= startSum && temSum <= endSum) {
          if (!allContacts.has(val["name"])) setNewContacts.add(val["name"]);
        }
      }
      console.log(setNewContacts);
      this.newContacts = setNewContacts.size;

      //作图
      var temOrders = [];
      for (let val of this.ordersData) {
        let temVal = val["createTime"].split("-");
        let temSum =
          Number(temVal[0]) * 12 * 30 +
          Number(temVal[1]) * 30 +
          Number(temVal[2]);
        let startSum =
          Number(splitStartTime[0]) * 12 * 30 +
          Number(splitStartTime[1]) * 30 +
          Number(splitStartTime[2]);
        let endSum =
          Number(splitEndTime[0]) * 12 * 30 +
          Number(splitEndTime[1]) * 30 +
          Number(splitEndTime[2]);
        if (temSum >= startSum && temSum <= endSum) {
          temOrders.push(val);
        }
      }
      this.ordersDataCopy = [];
      this.ordersDataCopy = temOrders;

      let ordersValue = new Map();
      for (let val of this.ordersDataCopy) {
        console.log(val);
        if (ordersValue.has(val["createTime"])) {
          ordersValue.set(
            val["createTime"],
            ordersValue.get(val["createTime"]) + Number(val["orderValue"])
          );
        } else {
          ordersValue.set(val["createTime"], Number(val["orderValue"]));
        }
      }
      console.log(ordersValue);
      this.option["xAxis"]["data"] = [];
      this.option["series"][0]["data"] = [];
      this.option2["xAxis"]["data"] = [];
      this.option2["series"][0]["data"] = [];
      const iterator1 = ordersValue.entries();
      var index = 0;
      for (index; index < ordersValue.size; index++) {
        var tem = iterator1.next().value;
        this.option["xAxis"]["data"].push(tem[0]);
        this.option["series"][0]["data"].push(tem[1]);
        this.option2["xAxis"]["data"].push(tem[0]);
        this.option2["series"][0]["data"].push(tem[1]);
      }
      console.log(this.option["series"][0]["data"]);
    },
    exit() {
      this.$router.push({path: '/'});
    },
  },
};
</script>


