<script>
import dayjs from "dayjs";
import pubsub from "pubsub-js";
import {
  getYinXiao,
  setYinXiao,
  deleteYinXiao,
  getAllYinXiao,
  getAll,
  getAllusers,
  getSelectedPermission,
} from "@/api/index";
import axios from "axios";

export default {
  data() {
    return {
      //time: "2022-3-14 21:34:18",
      options: ["暂无"],
      showTopRight: false,
      tip: "营销机会管理",
      userName: "",
      baseShow: true,
      pwdShow: false,
      disabled1: true,
      disabled2: false,
      customer: "",
      creater: "",
      status: ["全部", "已分配", "未分配"],
      tableData: [],
      statusValue: "全部",
      formData: {},
      newData: {},
      formDataCopy: {},
      dialogShow: false, //弹出层默认为关闭
      dialogShow2: false,
      //弹出层中的表单
      form: {
        account: "",
        username: "",
        id: "",
      },
      formLabelWidth: "50px",
      selectedIds: [],
      quanxian: {},
      yinxiao: false,
      yinxiao11: false,
      yinxiao12: false,
      yinxiao21: false,
      yinxiao22: false,
      shuju: false,
      kehu: false,
      kehu11: false,
      kehu12: false,
      kehu21: false,
      kehu22: false,
      xitong: false,
      xitong11: false,
      xitong12: false,
      xitong21: false,
      xitong22: false,
    };
  },
  components: {
    //VTable, VPagination
  },
  props: {
    str: String,
    //  oldPWD: String,
  },
  async created() {
    this.userName = this.$route.query.str;

    let res = await getAllusers();
    console.log(res.data);
    for (let val of res.data) {
      console.log(this.$route.query.str);
      if (val["userName"] == this.$route.query.str) {
        let res_ = await getSelectedPermission(val["role"]);
        this.quanxian = res_.data;
        console.log(res_.data);
        break;
      }
    }
    console.log(this.quanxian);
    //console.log(this.quanxian)
    this.yinxiao = Number(JSON.parse(JSON.stringify(this.quanxian))[0].yinxiao);
    this.yinxiao11 = Number(
      JSON.parse(JSON.stringify(this.quanxian))[0].yinxiao11
    );
    this.yinxiao12 = Number(
      JSON.parse(JSON.stringify(this.quanxian))[0].yinxiao12
    );
    this.yinxiao21 = Number(
      JSON.parse(JSON.stringify(this.quanxian))[0].yinxiao21
    );
    this.yinxiao22 = Number(
      JSON.parse(JSON.stringify(this.quanxian))[0].yinxiao22
    );
    this.shuju = Number(JSON.parse(JSON.stringify(this.quanxian))[0].shuju);
    this.kehu = Number(JSON.parse(JSON.stringify(this.quanxian))[0].kehu);
    this.kehu11 = Number(JSON.parse(JSON.stringify(this.quanxian))[0].kehu11);
    this.kehu12 = Number(JSON.parse(JSON.stringify(this.quanxian))[0].kehu12);
    this.kehu21 = Number(JSON.parse(JSON.stringify(this.quanxian))[0].kehu21);
    this.kehu22 = Number(JSON.parse(JSON.stringify(this.quanxian))[0].kehu22);
    this.xitong = Number(JSON.parse(JSON.stringify(this.quanxian))[0].xitong);
    this.xitong11 = Number(
      JSON.parse(JSON.stringify(this.quanxian))[0].xitong11
    );
    this.xitong12 = Number(
      JSON.parse(JSON.stringify(this.quanxian))[0].xitong12
    );
    this.xitong21 = Number(
      JSON.parse(JSON.stringify(this.quanxian))[0].xitong21
    );
    this.xitong22 = Number(
      JSON.parse(JSON.stringify(this.quanxian))[0].xitong22
    );
    //console.log(this.quanxian)
  },
  async mounted() {
    pubsub.subscribe("sendMsg", (e, msg) => {
      this.showTopRight = msg;
    });

    pubsub.subscribe("sendUsername", (e, msg) => {
      this.userName = msg;
    });

    let allInfo = await getAll();
    var i;
    for (i = 0; i < allInfo.data.length; i++) {
      this.options.push(allInfo.data[i]["userName"]);
    }

    let res = await getAllYinXiao();
    this.tableData = res.data;
    let res1 = await getYinXiao(
      this.newData.customerName,
      this.newData.creator,
      this.newData.status
    );
    this.tableData.splice(1, 0, res1[0].data);
  },
  methods: {
    tableSelected(val) {
      for (let tem of val) {
        this.selectedIds.push(tem.id);
      }
    },
    addItem() {
      this.dialogShow2 = true;
      this.newData["allocator"] = "";
    },
    async submitForm_() {
      this.dialogShow2 = false;
      //setYinXiao(this.formData);
      this.newData["creator"] = this.userName;
      this.newData["exploitStatus"] = "待开发";
      let time = dayjs().format("YYYY-MM-DD");
      this.newData["createTime"] = time;
      console.log(this.str);
      if (this.newData["allocator"] == "暂无") {
        this.newData["status"] = 0;
        this.newData["allocateTime"] = "";
      } else {
        this.newData["status"] = 1;
        this.newData["allocateTime"] = time;
      }
      console.log(this.newData);
      console.log(this.newData["creator"]);
      axios
        .post("http://localhost:8081/user/addYinXiao", this.newData)
        .then(function (response) {
          console.log(response);
        });
      this.newData = {};
      this.$router.go(0);
    },
    async search() {
      console.log(this.customer + " " + this.creater + " " + this.statusValue);

      if (this.statusValue == "已分配") {
        if (this.customer == "" && this.creater == "") {
          let res = await getYinXiao("a", "b", 1);
          this.tableData = res.data;
          console.log(res.data);
        } else if (this.creater == "") {
          let res = await getYinXiao(this.customer, "b", 1);
          this.tableData = res.data;
        } else if (this.customer == "") {
          let res = await getYinXiao("a", this.creater, 1);
          this.tableData = res.data;
        } else {
          let res = await getYinXiao(this.customer, this.creater, 1);
          this.tableData = res.data;
        }
      } else if(this.statusValue == "未分配"){
        if (this.customer == "" && this.creater == "") {
          let res = await getYinXiao("a", "b", 0);
          this.tableData = res.data;
        } else if (this.creater == "") {
          let res = await getYinXiao(this.customer, "b", 0);
          this.tableData = res.data;
        } else if (this.customer == "") {
          let res = await getYinXiao("a", this.creater, 0);
          this.tableData = res.data;
        } else {
          let res = await getYinXiao(this.customer, this.creater, 0);
          this.tableData = res.data;
        }
      }
      else {
        let res = await getAllYinXiao();
        this.tableData = res.data;
        console.log(res.data);
      }
      //this.$router.go(0);
    },

    deleteClick() {},
    handleEdit(row) {
      console.log(row);
      this.dialogShow = true;
      console.log(this.dialogShow);
      this.formData = Object.assign({}, row);
      this.formDataCopy = Object.assign({}, row);
    },
    async submitForm() {
      this.dialogShow = false;
      let index = this.tableData.findIndex(
        (item, index) => item.id === this.formData.id
      );
      console.log(index);
      this.formData["allocateTime"] = dayjs().format("YYYY-MM-DD");
      if(this.formData['allocator'] == "暂无")
        this.formData["status"] = 0;
      else
        this.formData["status"] = 1;
      this.tableData.splice(index, 1, this.formData);
      //setYinXiao(this.formData);
      axios
        .post("http://localhost:8081/user/setYinXiao", this.formData)
        .then(function (response) {
          console.log(response);
        });
      let res = await getAllYinXiao();
      this.tableData = res.data;
      this.$router.go(0);
    },
    resetForm() {
      this.formData = Object.assign({}, this.formDataCopy);
    },
    confirmEvent(val) {
      let index = this.tableData.findIndex((item) => item.id === val.id);
      this.tableData.splice(index, 1);
      deleteYinXiao(val.id);
      this.$router.go(0);
    },
    async deleteAll() {
      if (confirm("确定要删除吗") == true) {
        for (let id of this.selectedIds) {
          deleteYinXiao(id);
        }
      }
      let res = await getAllYinXiao();
      this.tableData = res.data;
      this.$router.go(0);
    },
    // async myconfirm() {
    //   if(confirm("que"))
    // },
    exit() {
      this.$router.push({path: '/'});
    }
  },
};
</script>


