<template>
  <!-- 
  客户管理页面包括三个模块，
  第一个模块是所有页面共有页面，即init-show
  第二个模块是客户信息管理页面专有模块，即kehuxinxi
  第三个模块是订单管理页面专有模块，即shangjiguanli
 -->
  <div class="KeHu">
    <init-show @func="getMsgKHKF"></init-show>
    <kehuxinxi v-if="flag1"></kehuxinxi>
    <shangjiguanli v-if="flag2"></shangjiguanli>
  </div>
</template>

<script>
import initShow from "@/components/initShow.vue";
import shangjiguanli from "@/components/shangjiguanli.vue";
import kehuxinxi from "@/components/kehuxinxi.vue";
export default {
  //数据
  data() {
    return {
      flag1: true,
      flag2: false,
    };
  },
  //自定义模块
  components: {
    initShow,
    shangjiguanli,
    kehuxinxi,
  },
  //页面更新时执行的函数
  mounted() {},
  //自定义的函数
  methods: {
    getMsgKHKF(data) {
      if (data == 2) {
        this.flag2 = true;
        this.flag1 = false;
      } else {
        this.flag2 = false;
        this.flag1 = true;
      }
    },
  },
};
</script>
