<script>
import dayjs from "dayjs";
import pubsub from "pubsub-js";
import {
  getSelectedPermission,
  addRole,
  getAllusers,
  getAllroles,
  deleteArole,
  setNewRole,
  setNewUser,
} from "@/api/index";
import axios from "axios";

export default {
  data() {
    return {
      quanxian: {
        yinxiao: [],
        xitong: [],
        kehu: [],
        shuju: [],
      },
      selectedValue: 0,
      selectedRole: "",
      selectedRolePre: "",
      showTopRight: false,
      tip: "角色管理",
      roles: [
        "超级管理员",
        "系统设置管理员",
        "销售员",
        "销售经理",
        "运维人员",
        "财务员",
      ],
      submitData: {},
      dialogShow: false,
      dialogShow2: false,
      newRole: "",
      rolesData: [],
      quanxian: {},
      yinxiao: false,
      yinxiao11: false,
      yinxiao12: false,
      yinxiao21: false,
      yinxiao22: false,
      shuju: false,
      kehu: false,
      kehu11: false,
      kehu12: false,
      kehu21: false,
      kehu22: false,
      xitong: false,
      xitong11: false,
      xitong12: false,
      xitong21: false,
      xitong22: false,
    };
  },
  components: {},
  props: {
    str: String,
  },
  async created() {
    let res = await getAllroles();
    console.log(res.data);
    this.roles = [];
    this.rolesData = res.data;
    for (let val of res.data) {
      this.roles.push(val.role);
    }

    let res__ = await getAllusers();
    console.log(res__.data);
    for (let val of res__.data) {
      console.log(this.$route.query.str);
      if (val["userName"] == this.$route.query.str) {
        let res_ = await getSelectedPermission(val["role"]);
        this.quanxian = res_.data;
        console.log(res_.data);
        break;
      }
    }
    console.log(this.quanxian);
    //console.log(this.quanxian)
    this.yinxiao = Number(JSON.parse(JSON.stringify(this.quanxian))[0].yinxiao);
    this.yinxiao11 = Number(
      JSON.parse(JSON.stringify(this.quanxian))[0].yinxiao11
    );
    this.yinxiao12 = Number(
      JSON.parse(JSON.stringify(this.quanxian))[0].yinxiao12
    );
    this.yinxiao21 = Number(
      JSON.parse(JSON.stringify(this.quanxian))[0].yinxiao21
    );
    this.yinxiao22 = Number(
      JSON.parse(JSON.stringify(this.quanxian))[0].yinxiao22
    );
    this.shuju = Number(JSON.parse(JSON.stringify(this.quanxian))[0].shuju);
    this.kehu = Number(JSON.parse(JSON.stringify(this.quanxian))[0].kehu);
    this.kehu11 = Number(JSON.parse(JSON.stringify(this.quanxian))[0].kehu11);
    this.kehu12 = Number(JSON.parse(JSON.stringify(this.quanxian))[0].kehu12);
    this.kehu21 = Number(JSON.parse(JSON.stringify(this.quanxian))[0].kehu21);
    this.kehu22 = Number(JSON.parse(JSON.stringify(this.quanxian))[0].kehu22);
    this.xitong = Number(JSON.parse(JSON.stringify(this.quanxian))[0].xitong);
    this.xitong11 = Number(
      JSON.parse(JSON.stringify(this.quanxian))[0].xitong11
    );
    this.xitong12 = Number(
      JSON.parse(JSON.stringify(this.quanxian))[0].xitong12
    );
    this.xitong21 = Number(
      JSON.parse(JSON.stringify(this.quanxian))[0].xitong21
    );
    this.xitong22 = Number(
      JSON.parse(JSON.stringify(this.quanxian))[0].xitong22
    );
  },
  async mounted() {
    console.log(this.quanxian.yinxiao);
    pubsub.subscribe("sendMsg", (e, msg) => {
      this.showTopRight = msg;
    });
  },
  methods: {
    save() {
      this.yinxiao1 = true;
      console.log(this.quanxian.xitong);
      // for(let val of this.submitData) {
      //     if(val != "role" && val != "id") {
      //         this.submitData[val] = "0"
      //     }
      // }
      this.submitData["role"] = this.selectedRole;
      this.submitData["yinxiao"] = "0";
      this.submitData["yinxiao11"] = "0";
      this.submitData["yinxiao12"] = "0";
      this.submitData["yinxiao21"] = "0";
      this.submitData["yinxiao22"] = "0";
      this.submitData["xitong"] = "0";
      this.submitData["xitong11"] = "0";
      this.submitData["xitong12"] = "0";
      this.submitData["xitong21"] = "0";
      this.submitData["xitong22"] = "0";
      this.submitData["kehu"] = "0";
      this.submitData["kehu11"] = "0";
      this.submitData["kehu12"] = "0";
      this.submitData["kehu21"] = "0";
      this.submitData["kehu22"] = "0";
      this.submitData["shuju"] = "0";
      for (let val of this.quanxian.yinxiao) {
        if (val == "查看") this.submitData["yinxiao"] = "1";
        if (val == "营销机会查看") this.submitData["yinxiao11"] = "1";
        if (val == "营销机会编辑") this.submitData["yinxiao12"] = "1";
        if (val == "客户开发计划查看") this.submitData["yinxiao21"] = "1";
        if (val == "客户开发计划编辑") this.submitData["yinxiao22"] = "1";
      }

      for (let val of this.quanxian.xitong) {
        if (val == "查看") this.submitData["xitong"] = "1";
        if (val == "用户查看") this.submitData["xitong11"] = "1";
        if (val == "用户编辑") this.submitData["xitong12"] = "1";
        if (val == "角色权限查看") this.submitData["xitong21"] = "1";
        if (val == "角色权限编辑") this.submitData["xitong22"] = "1";
      }

      for (let val of this.quanxian.kehu) {
        if (val == "查看") this.submitData["kehu"] = "1";
        if (val == "客户信息查看") this.submitData["kehu11"] = "1";
        if (val == "客户信息编辑") this.submitData["kehu12"] = "1";
        if (val == "订单查看") this.submitData["kehu21"] = "1";
        if (val == "订单编辑") this.submitData["kehu22"] = "1";
      }
      if (this.quanxian.shuju.length) this.submitData["shuju"] = "1";

      axios
        .post("http://localhost:8081/user/setPermission", this.submitData)
        .then(function (response) {
          console.log(response);
        });
    },
    async selected(i, role) {
      this.selectedRole = role;
      this.selectedValue = i;
      console.log(role);
      let res = await getSelectedPermission(role);
      console.log(res.data[0]);
      this.quanxian.yinxiao = [];
      this.quanxian.kehu = [];
      this.quanxian.xitong = [];
      this.quanxian.shuju = [];
      if (res.data[0].yinxiao == "1") this.quanxian.yinxiao.push("查看");
      if (res.data[0].yinxiao11 == "1")
        this.quanxian.yinxiao.push("营销机会查看");
      if (res.data[0].yinxiao12 == "1")
        this.quanxian.yinxiao.push("营销机会编辑");
      if (res.data[0].yinxiao21 == "1")
        this.quanxian.yinxiao.push("客户开发计划查看");
      if (res.data[0].yinxiao22 == "1")
        this.quanxian.yinxiao.push("客户开发计划编辑");
      if (res.data[0].shuju == "1") this.quanxian.shuju.push("查看");
      if (res.data[0].kehu == "1") this.quanxian.kehu.push("查看");
      if (res.data[0].kehu11 == "1") this.quanxian.kehu.push("客户信息查看");
      if (res.data[0].kehu12 == "1") this.quanxian.kehu.push("客户信息编辑");
      if (res.data[0].kehu21 == "1") this.quanxian.kehu.push("订单查看");
      if (res.data[0].kehu22 == "1") this.quanxian.kehu.push("订单编辑");

      if (res.data[0].xitong == "1") this.quanxian.xitong.push("查看");
      if (res.data[0].xitong11 == "1") this.quanxian.xitong.push("用户查看");
      if (res.data[0].xitong12 == "1") this.quanxian.xitong.push("用户编辑");
      if (res.data[0].xitong21 == "1")
        this.quanxian.xitong.push("角色权限查看");
      if (res.data[0].xitong22 == "1")
        this.quanxian.xitong.push("角色权限编辑");
    },
    create() {
      this.dialogShow = true;
    },
    async itemSave() {
      console.log(this.newRole);
      addRole(this.newRole);
      this.dialogShow = false;
      let res = await getAllroles();
      console.log(res.data);
      this.roles = [];
      for (let val of res.data) {
        this.roles.push(val.role);
      }
    },
    async deleteRole() {
      if (confirm("确定要删除吗？") == true) {
        deleteArole(this.selectedRole);
      }

      let res = await getAllroles();
      console.log(res.data);
      this.roles = [];
      for (let val of res.data) {
        this.roles.push(val.role);
      }
    },
    edit() {
      this.dialogShow2 = true;
      this.selectedRolePre = this.selectedRole;
    },
    editSave() {
      this.dialogShow2 = false;
      setNewRole(this.selectedRolePre, this.selectedRole);
      setNewUser(this.selectedRolePre, this.selectedRole);
    },
    editCancel() {
      this.dialogShow2 = false;
    },
    exit() {
      this.$router.push({path: '/'});
    },
  },
};
</script>


