<template>
  <div class="xitong">
    <init-show @func="getMsgKHKF"></init-show>
    <yonghuguanli v-if="flag1"></yonghuguanli>
    <juesequanxian v-if="flag2"></juesequanxian>
  </div>
</template>

<script>
import initShow from "@/components/initShow.vue";
import yonghuguanli from "@/components/yonghuguanli.vue";
import juesequanxian from "@/components/juesequanxian.vue";
//系统管理页面
export default {
  components: {
    initShow,
    yonghuguanli,
    juesequanxian,
  },
  data() {
    return {
      flag1: true,
      flag2: false,
    };
  },
  methods: {
    getMsgKHKF(data) {
      if (data == 2) {
        this.flag2 = true;
        this.flag1 = false;
      } else {
        this.flag2 = false;
        this.flag1 = true;
      }
    },
  },
};
</script>

