<script>
//登录页面
import axios from "axios"
import {getAll, getUserById} from "@/api/index"
import pubsub from "pubsub-js";
import {getCurrentInstance} from "@vue/runtime-core";

export default {
  data() {
    return {
      isChecked: false,
      userNameCorr: "",
      pwdCorr: "",
      userName:"tjj",
      pwd:"",
    };
  },
  methods: {
    cleanChecked(event) {
      if (this.isChecked == false) {
        this.isChecked = true;
      } else {
        this.isChecked = false;
      }

    },
    async loginIn() {
      
        let res = await getUserById(this.userName)

        this.userNameCorr = res.data['userName']
        this.pwdCorr = res.data['pwd']
        //console.log("sdsdsd")
        console.log(res.data)
        console.log(this.userNameCorr)
        if(this.pwd == this.pwdCorr && this.userNameCorr == this.userName){
          this.$cookies.set("username", this.userNameCorr);
          console.log(this.$cookies.get('username'))
          this.$router.push({path: '/home', query: {str: this.userNameCorr, pwd: this.pwdCorr}});
        }
        else {
            alert("username or pwd not correct!")
        }
    },
  },
};
</script>


